<template>
<example-attack :my-list="list">
  <template #Midfielder>
    <p class="font-bold text-white font-black w-full">هافبک های تیم منچستریونایتد</p>
  </template>
</example-attack>
</template>

<script>
import {onMounted, reactive} from "vue";
import axios from "axios";
import ExampleAttack from "@/components/main/ExampleAttack";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "AttackMan",
  components: {ExampleAttack},
  setup(){
    const list=reactive([]);
    const total=onMounted(async ()=>{
      await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/127'),
        axios.get('https://api.footballi.net/api/v2/player/966'),
        axios.get('https://api.footballi.net/api/v2/player/1826'),
        axios.get('https://api.footballi.net/api/v2/player/2674'),
        axios.get('https://api.footballi.net/api/v2/player/3826'),
        axios.get('https://api.footballi.net/api/v2/player/6777'),
        axios.get('https://api.footballi.net/api/v2/player/2222588'),
        axios.get('https://api.footballi.net/api/v2/player/2404956'),
        axios.get('https://api.footballi.net/api/v2/player/2451789'),
      ])
          .then(axios.spread((...res)=>{
            list.push(res)
          }))
          .catch((error)=>{
            handleError(error)
          })
    })
    return{
      list,total
    }
  }
}
</script>

