<template>
<example-transfer :my-list="menu">

</example-transfer>
</template>

<script>
import {reactive,onMounted} from "vue";
import axios from "axios";
import ExampleTransfer from "@/components/main/ExampleTransfer";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "TransferTeamMan",
  components: {ExampleTransfer},
  setup(){
    const menu=reactive([])
    const sum=onMounted(async ()=>{
      await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/966'),
        axios.get('https://api.footballi.net/api/v2/player/2364789'),
        axios.get('https://api.footballi.net/api/v2/player/5714'),
        axios.get('https://api.footballi.net/api/v2/player/709'),
        axios.get('https://api.footballi.net/api/v2/player/28847'),
        axios.get('https://api.footballi.net/api/v2/player/106'),
        axios.get('https://api.footballi.net/api/v2/player/843'),
        axios.get('https://api.footballi.net/api/v2/player/6777'),
        axios.get('https://api.footballi.net/api/v2/player/28009'),
        axios.get('https://api.footballi.net/api/v2/player/2260610'),
        axios.get('https://api.footballi.net/api/v2/player/709'),
        axios.get('https://api.footballi.net/api/v2/player/16686'),
        axios.get('https://api.footballi.net/api/v2/player/2895'),
        axios.get('https://api.footballi.net/api/v2/player/3933'),
      ])
          .then(axios.spread((...response)=>{
            menu.push(response)
          }))
          .catch((error)=>{
            handleError(error)
          })
    })
    return{
      menu,sum
    }
  }
}
</script>

