<template>
<example-team-rank :my-list="list">
<template #rankName>
  <option class="font-bold text-slate-900 text-sm text-start">لیگ برتر انگلیس</option>
</template>
</example-team-rank>
</template>

<script>
import {onMounted, reactive} from "vue";
import EndPoints from "@/service/EndPoints";
import exampleTeamRank from "@/components/main/ExampleTeamRank";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "RankManchester",
  components:{exampleTeamRank},
  setup(){
    const list=reactive([])
    const result=onMounted(()=>{
      EndPoints.getDescriptionMan()
          .then((response)=>{
            if(response.data.success){
              list.push(response.data.data.standing)
            }
          })
          .catch((error)=>{
         handleError(error)
          })

    })
    return{
      list,result,
    }
  }
}
</script>

