<template>
<example-super-goal :my-list="players">

</example-super-goal>
</template>

<script>
import {onMounted, reactive} from "vue";
import axios from "axios";
import ExampleSuperGoal from "@/components/main/ExampleSuperGoal";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "SuperGoalMan",
  components: {ExampleSuperGoal},
  setup(){
    const players=reactive([])
    const total=onMounted(async ()=>{
     await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/30151'),
        axios.get('https://api.footballi.net/api/v2/player/1826'),
        axios.get('https://api.footballi.net/api/v2/player/2467'),
        axios.get('https://api.footballi.net/api/v2/player/2288931'),
      ])
          .then(axios.spread((...response)=>{
            players.push(response)
          }))
          .catch((error)=>{
           handleError(error)
          })

    })
    return{
      players,total
    }
  }
}
</script>

