<template>
<example-main-attack :my-list="list">
  <template #attacker>
    <p class="font-bold text-white font-black w-full">مهاجمان تیم منچستریونایتد</p>
  </template>
</example-main-attack>
</template>

<script>
import ExampleMainAttack from "@/components/main/ExampleMainAttack";
import {onMounted, reactive} from "vue";
import axios from "axios";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "MainAttack",
  components: {ExampleMainAttack},
  setup(){
    const list=reactive([])
    const sum=onMounted(async ()=>{
      await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/843'),
        axios.get('https://api.footballi.net/api/v2/player/2467'),
        axios.get('https://api.footballi.net/api/v2/player/30151'),
        axios.get('https://api.footballi.net/api/v2/player/2222704'),
        axios.get('https://api.footballi.net/api/v2/player/22258098'),
        axios.get('https://api.footballi.net/api/v2/player/2288931'),
        axios.get('https://api.footballi.net/api/v2/player/2317764'),
        axios.get('https://api.footballi.net/api/v2/player/2336945'),
        axios.get('https://api.footballi.net/api/v2/player/2364775'),
      ])
          .then(axios.spread((...res)=>{
            list.push(res)
          }))
          .catch((error)=>{
           handleError(error)
          })

    })
    return{
      list,sum,
    }
  }
}
</script>

