<template>
<example-defender :my-list="list">
  <template #defender>
    <p class="font-bold text-white font-black w-full">مدافعان تیم منچستریونایتد</p>
  </template>
</example-defender>
</template>

<script>
import ExampleDefender from "@/components/main/ExampleDefender";
import {onMounted, reactive} from "vue";
import axios from "axios";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "DefenderMan",
  components: {ExampleDefender},
  setup(){
    const list=reactive([])
    const result=onMounted(async ()=>{
      await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/320'),
        axios.get('https://api.footballi.net/api/v2/player/343'),
        axios.get('https://api.footballi.net/api/v2/player/2895'),
        axios.get('https://api.footballi.net/api/v2/player/2222302'),
        axios.get('https://api.footballi.net/api/v2/player/2222682'),
        axios.get('https://api.footballi.net/api/v2/player/2223061'),
        axios.get('https://api.footballi.net/api/v2/player/2233006'),
        axios.get('https://api.footballi.net/api/v2/player/22257346'),
        axios.get('https://api.footballi.net/api/v2/player/2268275'),
        axios.get('https://api.footballi.net/api/v2/player/2364766'),
        axios.get('https://api.footballi.net/api/v2/player/2364767'),
      ])
          .then(axios.spread((...response)=>{
            list.push(response)
          }))
          .catch((error)=>{
           handleError(error)
          })

    })
    return{
      list,result,
    }
  }
}
</script>

