<template>
<example-team-news :my-list="list">

</example-team-news>
</template>

<script>
import {onMounted, reactive} from "vue";
import EndPoints from "@/service/EndPoints";
import ExampleTeamNews from "@/components/main/ExampleTeamNews";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "LastNewsMan",
  components: {ExampleTeamNews},
  setup(){
    const list=reactive([])
    const total=onMounted(()=>{
      EndPoints.getNewsMan()
          .then((response)=>{
            if(response.data.success){
              list.push(response.data.data)

            }
          })
          .catch((error)=>{
            handleError(error)
          })

    })
    return{
      list,total,
    }
  }
}
</script>

