<template>
<section class="center-man">
  <div class="container mx-auto p-2">
    <div class="w-full h-auto">
      <p class="font-semibold text-start text-black w-full">بازیکنان تیم منچستریونایتد</p>
      <div class="bg-black h-0.5 w-full my-1"></div>
      <div class="bg-green-500 w-full min-h-full relative px-3">
        <example-goal-keeper :my-list="result">
          <template #goalKeeper>
            <p class="font-bold text-white font-black w-full">دروازه بان های تیم منچستریونایتد</p>
          </template>
        </example-goal-keeper>
        <defender-man></defender-man>
        <attack-man></attack-man>
        <main-attack></main-attack>
        <img :src="require('../../../assets/image/squad-middle.png')" alt="" class="h-auto absolute mx-auto bottom-2.5 w-full left-0 right-0">
      </div>

    </div>
  </div>
</section>
</template>

<script>
import {onMounted, reactive} from "vue";
import axios from "axios";
import ExampleGoalKeeper from "@/components/main/ExampleGoalKeeper";
import DefenderMan from "@/components/main/TeamManchester/DefenderMan";
import AttackMan from "@/components/main/TeamManchester/AttackMan";
import MainAttack from "@/components/main/TeamManchester/MainAttack";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "PlayersMan",
  components:{MainAttack, AttackMan, DefenderMan, ExampleGoalKeeper},
  setup(){
    const result=reactive([]);
    const total=onMounted(async ()=>{
      await axios.all([
        axios.get('https://api.footballi.net/api/v2/player/286'),
        axios.get('https://api.footballi.net/api/v2/player/5714'),
        axios.get('https://api.footballi.net/api/v2/player/2222488'),
      ])
          .then(axios.spread((...response)=>{
            result.push(response)
          }))
          .catch((error)=>{
            handleError(error)
          })

    })
    return{
      result,total
    }
  }
}
</script>

